.tabs {
    margin: 20px 0;
  }
  
  .tab-buttons button {
    padding: 10px 20px;
    margin-right: 5px;
    background-color: #e0e0e0;
    border: none;
    cursor: pointer;
    font-size: 16px;
  }
  
  .tab-buttons button.active {
    background-color: #2e7d32;
    color: white;
  }
  
  .tab-content {
    margin-top: 20px;
  }
  