.visualizacao {
    padding: 20px;
  }
  
  .visualizacao canvas {
    margin-bottom: 20px;
    max-width: 100%;
  }
  
  .data-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .data-table th,
  .data-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .data-table th {
    background-color: #2e7d32;
    color: white;
  }
  
  .download-button {
    background-color: #2e7d32;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .download-button:hover {
    background-color: #1b5e20;
  }
  